/* Importing Montserrat font */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap");

/* Base Styles */
body {
  font-family: "Montserrat", sans-serif;
  /* Dark, technical gradient background with subtle noise */
  background: linear-gradient(
    to bottom right,
    #0f172a,
    #1e293b
  ); /* Deep blue-grey */
  color: #e2e8f0; /* Light grey for overall text */
  display: flex;
  flex-direction: column; /* Changed to column to stack navbar and content */
  justify-content: flex-start; /* Align content to start */
  min-height: 100vh;
  margin: 0;
  padding: 0; /* Remove body padding, moved to main-container */
  box-sizing: border-box;
  overflow-y: auto; /* Allow scrolling if content is long */
}

/* Subtle background noise texture */
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml;base64,")
    repeat;
  opacity: 0.1;
  z-index: -1;
}

.main-container {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: center;
  width: 100%;
  max-width: 960px; /* Increased max-width for the portfolio layout */
  margin: 20px auto; /* Add vertical margin */
}

.page-wrapper {
  flex-grow: 1; /* Allow wrapper to take available space */
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 20px; /* Padding for the page content itself */
  box-sizing: border-box;
}

/* --- Navigation Bar Styles --- */
.navbar {
  width: 100%;
  background-color: #1a202c; /* Dark background */
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.4);
  border-bottom: 1px solid #2d3748; /* Subtle border */
  position: sticky;
  top: 0;
  z-index: 100;
}

.navbar-brand {
  font-size: 1.8em;
  font-weight: 700;
  color: #4ade80; /* Green highlight */
  cursor: pointer;
  text-shadow: 0 0 5px rgba(74, 222, 128, 0.5);
  transition: color 0.3s ease;
}

.navbar-brand:hover {
  color: #60f090; /* Lighter green on hover */
}

.navbar-nav {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 25px; /* Space between navigation items */
}

.nav-item {
  position: relative;
}

.nav-link {
  background: none;
  border: none;
  color: #e2e8f0; /* Light text for links */
  font-size: 1em;
  font-weight: 500;
  padding: 8px 12px;
  cursor: pointer;
  transition: color 0.3s ease, background-color 0.3s ease;
  border-radius: 6px;
  text-decoration: none; /* For button acting as link */
}

.nav-link:hover {
  color: #4ade80; /* Green on hover */
  background-color: rgba(74, 222, 128, 0.1); /* Subtle background on hover */
}

.nav-link.active {
  color: #4ade80; /* Active link color */
  background-color: rgba(74, 222, 128, 0.2);
  box-shadow: 0 0 8px rgba(74, 222, 128, 0.4);
}

/* --- Home Page Styles --- */
.homepage-container {
  width: 100%;
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: calc(100vh - 100px); /* Adjust based on navbar height */
  padding: 40px;
  box-sizing: border-box;
  background-color: #1f2937; /* Dark card background */
  border-radius: 15px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  border: 1px solid #334155;
}

.hero-section {
  padding: 40px;
}

.hero-title {
  font-size: 3.5em;
  font-weight: 700;
  color: #e2e8f0;
  margin-bottom: 20px;
  line-height: 1.2;
}

.hero-title .highlight {
  color: #4ade80; /* Green highlight */
  text-shadow: 0 0 15px rgba(74, 222, 128, 0.8);
}

.hero-subtitle {
  font-size: 1.2em;
  color: #a0aec0;
  margin-bottom: 40px;
  line-height: 1.8;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-button {
  background-color: #3b82f6; /* Blue button */
  color: #ffffff;
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  font-size: 1.1em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease,
    box-shadow 0.3s ease;
  font-weight: 600;
  letter-spacing: 0.5px;
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.5);
  margin: 10px;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
}

.hero-button i {
  font-size: 1.1em;
}

.hero-button:hover {
  background-color: #2563eb;
  transform: translateY(-3px);
  box-shadow: 0 12px 25px rgba(59, 130, 246, 0.7);
}

.hero-button:active {
  transform: translateY(0);
  box-shadow: 0 4px 10px rgba(59, 130, 246, 0.3);
}

.hero-button.secondary {
  background-color: #475569; /* Slate grey secondary button */
  box-shadow: 0 8px 20px rgba(71, 85, 105, 0.4);
}

.hero-button.secondary:hover {
  background-color: #64748b;
  box-shadow: 0 12px 25px rgba(71, 85, 105, 0.6);
}

/* --- Form Card Styles --- */
.contact-card {
  /* Changed background to match technical aesthetic */
  background: linear-gradient(
    to bottom right,
    #1a202c,
    #2d3748
  ); /* Dark gradient */
  border-radius: 15px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.6); /* Darker shadow */
  width: 100%;
  padding: 40px;
  box-sizing: border-box;
  border: 1px solid #4a5568; /* Subtle border matching the dark theme */
  color: #e2e8f0; /* Ensure text inside is light */
}

.contact-card h1 {
  font-size: 2.4em;
  color: #4ade80; /* Vibrant green for main titles */
  margin-bottom: 15px;
  text-align: left;
  font-weight: 700;
  text-shadow: 0 0 8px rgba(74, 222, 128, 0.6); /* More prominent glow */
  letter-spacing: 1px;
}

.contact-card .subtitle {
  font-size: 1em;
  color: #a0aec0; /* Lighter grey for subtitles */
  margin-bottom: 30px;
  line-height: 1.6;
  text-align: left;
}

.form-section-header {
  font-size: 1.5em;
  color: #63b3ed; /* Softer blue for section headers */
  font-weight: 600;
  margin-top: 30px;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 2px solid #2b6cb0; /* Deeper blue underline */
  letter-spacing: 0.5px;
  position: relative;
}
/* Section header subtle glow */
.form-section-header::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 50px;
  height: 2px;
  background-color: #63b3ed;
  box-shadow: 0 0 10px rgba(99, 179, 237, 0.8);
}

.form-row {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  flex: 1;
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  font-size: 0.95em;
  color: #63b3ed; /* Blue for labels */
  margin-bottom: 8px;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.form-group input[type="text"],
.form-group input[type="email"],
.form-group textarea,
.form-group input[type="file"] {
  width: 100%;
  padding: 12px 15px;
  background-color: #2d3748; /* Darker background for inputs */
  border: 1px solid #4a5568; /* Darker border */
  border-radius: 8px;
  font-size: 1em;
  color: #e2e8f0; /* Light text for input content */
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease,
    background-color 0.3s ease;
  box-sizing: border-box;
}

.form-group input[type="file"] {
  cursor: pointer;
  padding-top: 10px; /* Adjust padding for file input */
  padding-bottom: 10px;
}

.form-group input[type="text"]::placeholder,
.form-group input[type="email"]::placeholder,
.form-group textarea::placeholder {
  color: #718096; /* Muted placeholder color */
}

.form-group input[type="text"]:focus,
.form-group input[type="email"]:focus,
.form-group textarea:focus,
.form-group input[type="file"]:focus {
  border-color: #63b3ed; /* Bright blue focus border */
  box-shadow: 0 0 0 3px rgba(99, 179, 237, 0.4); /* Prominent blue glow on focus */
  background-color: #334155; /* Slightly lighter on focus */
}

.form-group .image-preview-text {
  font-size: 0.85em;
  color: #a0aec0;
  margin-top: 5px;
  display: block;
}

.field-hint {
  font-size: 0.8em;
  color: #a0aec0;
  margin-top: 5px;
  display: block;
}

button#submitButton {
  background-color: #4ade80; /* Vibrant green button */
  color: #1a202c; /* Dark text for button */
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  font-size: 1.1em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease,
    box-shadow 0.3s ease;
  margin-top: 20px;
  width: 100%;
  font-weight: 700;
  letter-spacing: 0.8px;
  box-shadow: 0 8px 20px rgba(74, 222, 128, 0.5); /* Stronger green glow effect */
  text-transform: uppercase;
}

button#submitButton:hover {
  background-color: #22c55e; /* Darker green on hover */
  transform: translateY(-3px); /* More pronounced lift */
  box-shadow: 0 12px 25px rgba(74, 222, 128, 0.7);
}

button#submitButton:active {
  transform: translateY(0);
  box-shadow: 0 4px 10px rgba(74, 222, 128, 0.3);
}

/* --- Submitted Portfolio Card Styles (Reused for Preview and Samples) --- */
.submitted-portfolio-card {
  display: flex;
  flex-direction: column;
  background-color: #1f2937;
  border-radius: 15px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  width: 100%;
  overflow: hidden;
  animation: fadeIn 0.5s ease-out forwards;
  border: 1px solid #334155;
  color: #e2e8f0;
  margin-bottom: 30px; /* Space between sample cards */
}

.portfolio-header {
  background: linear-gradient(to right, #0f172a, #1e293b);
  padding: 40px;
  display: flex;
  align-items: center;
  gap: 30px;
  position: relative;
  overflow: hidden;
}

.portfolio-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
      to right,
      rgba(74, 222, 128, 0.05) 1px,
      transparent 1px
    ),
    linear-gradient(to bottom, rgba(74, 222, 128, 0.05) 1px, transparent 1px);
  background-size: 25px 25px;
  opacity: 0.1;
  z-index: 0;
}

.profile-photo-container {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-right: 20px;
}

.profile-photo-container .profile-image,
.profile-photo-container .fas.fa-user-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  background-color: #3b82f6;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5em;
  color: #e0f2f7;
  box-shadow: 0 0 0 5px rgba(59, 130, 246, 0.4),
    0 0 20px rgba(59, 130, 246, 0.6);
  border: 3px solid #60a5fa;
  margin-bottom: 15px;
}

.profile-photo-container .profile-image {
  border: 3px solid #60a5fa;
}

.header-name {
  font-size: 2.8em;
  font-weight: 700;
  color: #4ade80;
  line-height: 1.2;
  margin-bottom: 5px;
  text-shadow: 0 0 10px rgba(74, 222, 128, 0.7);
}

.header-title {
  font-size: 1.2em;
  color: #94a3b8;
  margin-bottom: 15px;
}

.header-bio {
  font-size: 1em;
  color: #cbd5e1;
  line-height: 1.6;
  max-width: 600px;
  margin-bottom: 25px;
  text-align: center;
}

.download-resume-button {
  background-color: #3b82f6;
  color: #ffffff;
  padding: 12px 25px;
  border: none;
  border-radius: 8px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease,
    box-shadow 0.3s ease;
  font-weight: 600;
  letter-spacing: 0.5px;
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
  display: flex;
  align-items: center;
  gap: 8px;
}

.download-resume-button:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(59, 130, 246, 0.6);
}

.download-resume-button:active {
  transform: translateY(0);
  box-shadow: 0 2px 5px rgba(59, 130, 246, 0.2);
}

/* Portfolio Content Sections */
.portfolio-content {
  padding: 40px;
  background-color: #1f2937;
  width: 100%;
  box-sizing: border-box;
}

.portfolio-section {
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 1px dashed #334155;
}

.portfolio-section:last-of-type {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.section-title {
  font-size: 2em;
  color: #e2e8f0;
  margin-bottom: 25px;
  font-weight: 700;
  display: flex;
  align-items: center;
  letter-spacing: 1px;
  position: relative;
  padding-bottom: 10px;
}

.section-title i {
  margin-right: 15px;
  color: #4ade80;
  font-size: 1.2em;
  text-shadow: 0 0 8px rgba(74, 222, 128, 0.6);
}

.section-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 4px;
  background: linear-gradient(to right, #3b82f6, #4ade80);
  border-radius: 2px;
}

/* Skills Section (Bar Chart) */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  padding-top: 10px;
}

.skill-bar-item {
  background-color: #2c3e50;
  border-radius: 8px;
  padding: 15px 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  border: 1px solid #3b4a5d;
  display: flex;
  flex-direction: column;
  gap: 8px;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.skill-bar-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.skill-name {
  font-size: 1em;
  color: #e2e8f0;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.progress-bar-container {
  background-color: #475569;
  border-radius: 5px;
  height: 12px;
  overflow: hidden;
  position: relative;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3);
}

.progress-bar {
  height: 100%;
  background: linear-gradient(to right, #4ade80, #3b82f6);
  border-radius: 5px;
  width: var(--skill-level, 0%);
  transition: width 1s ease-out;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 5px;
}

.skill-level-text {
  color: #ffffff;
  font-size: 0.75em;
  font-weight: 700;
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  mix-blend-mode: difference;
  position: absolute;
  right: 5px;
}

/* Featured Projects Section */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px;
  padding-top: 10px;
}

.project-card {
  background-color: #2c3e50;
  border-radius: 10px;
  padding: 25px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  border: 1px solid #3b4a5d;
  display: flex;
  flex-direction: column;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  position: relative;
  overflow: hidden;
}

.project-card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(to right, #4ade80, #3b82f6);
  opacity: 0;
  transform: translateY(-5px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.4);
}

.project-card:hover::before {
  opacity: 1;
  transform: translateY(0);
}

.project-card h3 {
  font-size: 1.5em;
  color: #60a5fa;
  margin-bottom: 10px;
  font-weight: 700;
  line-height: 1.3;
}

.project-card p {
  font-size: 0.95em;
  color: #cbd5e1;
  line-height: 1.5;
  margin-bottom: 15px;
  flex-grow: 1;
}

.project-tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.tech-tag {
  background-color: #334155;
  color: #e2e8f0;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 0.8em;
  font-weight: 500;
  border: 1px solid #475569;
  letter-spacing: 0.2px;
}

.view-details-button {
  background-color: #4ade80;
  color: #1a202c;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  font-size: 0.9em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  font-weight: 600;
  align-self: flex-start;
}

.view-details-button:hover {
  background-color: #22c55e;
  transform: translateY(-2px);
}
.view-details-button:active {
  transform: translateY(0);
}

/* Education Section */
.education-details {
  padding-top: 10px;
}

.education-details .detail-item {
  font-size: 1em;
  color: #cbd5e1;
  margin-bottom: 8px;
}

.education-details .detail-item strong {
  color: #e2e8f0;
  font-weight: 700;
}

/* Edit Portfolio Button (at the bottom) */
.edit-portfolio-button {
  background-color: #475569;
  color: #ffffff;
  padding: 12px 25px;
  border: none;
  border-radius: 8px;
  font-size: 1em;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease,
    box-shadow 0.3s ease;
  margin-top: 40px;
  align-self: flex-start;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: auto;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.edit-portfolio-button:hover {
  background-color: #64748b;
  transform: translateY(-1px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
}
.edit-portfolio-button:active {
  transform: translateY(0);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Temporary Message Box */
.temp-message-box {
  background-color: #4caf50;
  color: white;
  padding: 15px 25px;
  border-radius: 8px;
  font-family: "Montserrat", sans-serif;
  z-index: 1000;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.temp-message-box.error {
  background-color: #dc2626;
}

/* No data message for sections */
.no-data-message {
  color: #94a3b8;
  font-style: italic;
  text-align: center;
  padding: 20px;
  border: 1px dashed #475569;
  border-radius: 8px;
  margin-top: 20px;
  background-color: #1a202c;
}

/* Sample Portfolios Page specific styles */
.samples-container {
  width: 100%;
  max-width: 960px;
  margin: 20px auto;
  padding: 20px;
  box-sizing: border-box;
}

.page-title {
  font-size: 2.8em;
  font-weight: 700;
  color: #63b3ed; /* Blue for page titles */
  text-align: center;
  margin-bottom: 15px;
  text-shadow: 0 0 10px rgba(99, 179, 237, 0.5);
}

.page-subtitle {
  font-size: 1.1em;
  color: #a0aec0;
  text-align: center;
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}

.sample-portfolios-grid {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(450px, 1fr)
  ); /* Two columns for samples */
  gap: 30px;
  margin-top: 30px;
}

/* Fade in animation for any page content */
.fade-in {
  animation: fadeIn 0.5s ease-out forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive adjustments */
@media (max-width: 992px) {
  .navbar {
    padding: 15px 20px;
  }
  .portfolio-header {
    flex-direction: column;
    text-align: center;
    padding: 30px;
    gap: 20px;
  }
  .profile-photo-container {
    margin-right: 0;
  }
  .header-bio {
    max-width: 90%;
  }
  .samples-container {
    padding: 10px;
  }
  .sample-portfolios-grid {
    grid-template-columns: 1fr; /* Single column on smaller screens */
  }
}

@media (max-width: 768px) {
  .main-container {
    max-width: 720px;
  }
  .navbar-brand {
    font-size: 1.5em;
  }
  .navbar-nav {
    gap: 15px;
  }
  .nav-link {
    font-size: 0.9em;
    padding: 6px 10px;
  }
  .homepage-container {
    padding: 30px;
  }
  .hero-title {
    font-size: 2.5em;
  }
  .hero-subtitle {
    font-size: 1em;
  }
  .hero-button {
    font-size: 1em;
    padding: 12px 25px;
  }
  .contact-card,
  .submitted-portfolio-card {
    padding: 30px;
  }
  .contact-card h1 {
